import React, { useEffect, useState } from 'react';
import articleContent from './article-content';
import ArticlesList from "../components/articleList";
import NotFoundPage from "./NotFoundPage";
import CommentList from "../components/commentList";
import UpvoteSection from "../components/upvotesSection";
import AddCommentForm from "../components/addCommentForm";

const ArticlePage = ({ match }) => {
    const name = match.params.name;
    const article = articleContent.find(article => article.name === name);

    const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: []});

    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(`/api/articles/${name}`, {
                "headers": {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json'
                }
            });
            const body = await result.json();
            setArticleInfo(body);
        }
        fetchData();
    }, [name]);
    var commentsItem = <CommentList comments={articleInfo.comments} />;
    useEffect(() => {
        commentsItem = <span></span>;
        commentsItem = <CommentList comments={articleInfo.comments} />;
    }, [articleInfo]);
    if (!article) return <NotFoundPage />;

    const list = articleContent.filter((article) => article.name !== name);

    return (
        <>
        <h1>{article.title}</h1>
        <UpvoteSection articleName={name} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo}/>
        {article.content.map((paragraph, key) => (
            <p key={key}>{paragraph}</p>
        ))}
        {commentsItem}
        <AddCommentForm articleName={name} setArticleInfo={setArticleInfo} />
        <h3>Other Articles:</h3>
        <ArticlesList articles={list}/>
        </>
    );
}

export default ArticlePage;